.books-showcase{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 90%;
    background-color: #b2784a;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: 30px;
    margin-bottom: 30px;
    border-radius: 20px;
    padding-left: 10px;
    padding-right: 10px;
}

.book-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    color: white;
    margin-top: 30px;
    flex:5;
    padding-right: 20px;
    padding-left: 20px;
}

.title-label{
    margin-top: 5px;
    text-align: center;
    font-style: italic;
}

.author-label{
    text-align: center;
}

.expand-details-button{
    width: 100%;
    margin-top: 10px;
    background-color: #264653;
    color: white;
    border-radius: 20px;
    border: 0.5px white solid;
    margin-left: 5px;
}


